:root{
  --red:#E5243B;
  --mustard:#DDA63A;
  --kelly-green: #4C9F38;
  --dark-red: #C5192D;
  --red-orange: #FF3A21;
  --bright-blue: #26BDE2;
  --yellow: #FCC30B;
  --burgundy-red: #A21942;
  --orange: #FD6925;
  --magenta: #DD1367;
  --golden-yellow: #FD9D24;
  --dark-mustard: #BF8B2E;
  --dark-green: #3F7E44;
  --blue: #0A97D9;
  --lime-green: #56C02B;
  --royal-blue: #00689D;
  --navy-blue: #19486A;
}

/* Position and sizing of burger button */
.bm-burger-button {
  position: fixed;
  width: 36px;
  height: 30px;
  left: auto;
  left: initial;
  right: 36px;
  top: 36px;
}
 
/* Color/shape of burger icon bars */
.bm-burger-bars {
  background: #373a47;
}
 
/* Color/shape of burger icon bars on hover*/
.bm-burger-bars-hover {
  background: #a90000;
}
 
/* Position and sizing of clickable cross button */
.bm-cross-button {
  height: 24px;
  width: 24px;
}

/* Color/shape of close button cross */
.bm-cross {
  background: #bdc3c7;
}
 
/*
Sidebar wrapper styles
Note: Beware of modifying this element as it can break the animations - you should not need to touch it in most cases
*/
.bm-menu-wrap {
  position: fixed;
  height: 100%;
}
 
/* General sidebar styles */
.bm-menu {
  background: #373a47;
  padding: 2.5em 1.5em 0;
  font-size: 1.15em;
}
 
/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
  fill: #373a47;
}

/* Wrapper for item list */
.bm-item-list {
  color: #b8b7ad;
  padding: 0.8em;
}
 
/* Individual item */
.bm-item {
  display: inline-block;
}
 
/* Styling of overlay */
.bm-overlay {
  background: rgba(0, 0, 0, 0.3);
}

.bm-item-list a {
  padding: 0.8em;
  font-weight: 600;
}


.navbar-brand img {
  height: 70px;
}
.btn{
  letter-spacing: 0;
  border: 0px solid transparent;
  padding: 0.6rem 18px;
}

.btn-header{
  background-color: #fb6340;
  color: #fff;
}

.btn-header:hover{
  background-color: #ff4014;
  border-color: #ff4014;
}

.btn-primary{
  background-color: #34b5b8;
  border-color: #34b5b8;
  color: #fff;
}

.btn-primary:hover, 
.btn-primary.active{
  background-color: #227d80;
  border-color: #227d80;
}

.home-header .btn{
  height: 63px;
  line-height: 11px;
  font-size: 0.84rem;
  text-transform: capitalize;
}

.homeOverLay{
  
}

.home{
  background: url(../../static/media/trevor.f2252cf4.jpg);
  height: 100vh;
  background-size: cover;
  background-repeat: no-repeat;
}
.homeText{
  margin-top: 15%;
  margin-bottom: 2%;
}
.navbar{
  padding: 0.5rem 1rem;
}

.icon {
  width: 24px;
  height: 24px;
}

.sdg-icon-padding > .col, .sdg-icon-padding > [class*="col-"] {
  padding-right: 4px;
    padding-left: 4px;
}

.optionButtons > .col, .sdg-icon-padding > [class*="col-"]{
  padding-right: 0px;
  padding-left: 0px;
}
.optionButtons .form-control{
  border: 0px solid #cad1d7;
  border-radius: 0px;
  border-right: 1px solid #cad1d7;
  border-bottom: 1px solid #cad1d7;
  text-transform: uppercase;  
  font-weight: 600;
  padding: 0.25rem 0.75rem;
  height: calc(2rem + 2px);
}

.optionButtons  .lastChild .form-control{
  border-right: 0px solid #cad1d7;
}

/* Individual SDG 1  */
.sdgBackground{
  background: #e5233d;
  color: #fff;
}

.sdgBackground h5{
  color: #fff;
}

.yearSelect:active{
  background: #229699;
}

.selectButtons .btn:not(:disabled):not(.disabled){
  background-color: #34b5b8;
  border-color: #34b5b8;
  color: #fff;
}

.btn-primary:not(:disabled):not(.disabled):active, .btn-primary:not(:disabled):not(.disabled).active, .show > .btn-primary.dropdown-toggle{
  background-color: #227d80;
  border-color: #227d80;
