/* Large screens ----------- */
@media only screen and (min-width : 1400px) {
    .agenda2063Header .a2063-icon-padding .btn {
      padding: 5px;
      width: 97%;
  }
  .dashboard{
    padding-top: 15%;
  }
}

.files-div{
  padding: 10px 100px;
}

.file-div{
  padding: 10px 0px;
}

.file-div a{
  color: #32325d;
  padding-left: 5px;
  font-size: 1rem;
}

.files-div label{
  font-size: 1.5rem;
  font-weight: 600;
  color: #32325d;
}

.uploadFilesModal .form-control{
  font-size: 0.875rem;
  /* margin: 10px 0px; */
  height: calc(2.75rem);
  margin-bottom: 10px;
}

.uploadFilesModal .modal-body{
  padding: 15px;
}

.uploadFilesModal .custom-file-upload{
  border: 1px solid #cad1d7;
  border-radius: 0.25rem;
  padding: 0.625rem 0.75rem;
  margin-top: 0px;
}

.files-div  .card h3{
  text-align: center;
    font-weight: 600;
}

.files-div .card{
  padding: 20px;
  margin: 10px;
}

.files-div .card  .btn:not(:disabled):not(.disabled) {
  cursor: pointer;
  left: 50%;
  transform: translateX(-50%);
}

.uploadFilesModal .btn:not(:disabled):not(.disabled){
  left: 50%;
  transform: translateX(-50%);
}

.files-div .table{
  margin-top: 20px;
  table-layout: fixed;
}


.files-div .modal-dialog{
  padding-top: 150px;
}

.ReactTable{
  font-size: 0.8rem;
  margin-top: 10px;
}

.ReactTable .rt-td, .ReactTable .rt-th {
  padding: 6px 5px;
}

.file-div .btn-icon{
  padding: .1rem 1px;
  background-color: transparent;
  box-shadow: 0 4px 6px rgba(255, 255, 255, 0.11), 0 1px 3px rgba(255, 255, 255, 0.08);
}

.login-card{
  position: relative;
  max-width: 400px;
  margin: 20px 0 70px;
  /* min-height: 400px; */
  padding: 30px;
}

.input-group>.input-group-prepend>.input-group-text {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
  border-right: 0px;
}

.login-card form .btn {
  margin-top: 30px;
}

.forgot-password-link{
  margin-top: 30px;
}

.login-card label{
  margin-top: 15px;
}


.btn-round {
  border-radius: 30px;
}

.login-card .title{
  font-size: 1.45rem;
  font-weight: 600;
  margin-bottom: 20px;
}

.fa-175x{
  font-size: 1.75em;
  margin-bottom: 5px;
}

.btn-warning:not(:disabled):not(.disabled){
  color: rgb(255, 255, 255);
    background-color: rgb(253, 105, 37);
}

.login-card .btn-warning.btn-link{
  background-color: #fff;
  color: #fd6925;
  text-transform: uppercase;
  text-align: center;
}

 .error-message-label{
  color: red;
  font-weight: 400;
  /* line-height: 1.7; */
  font-size: 0.9rem;
  letter-spacing: 0.2px;
  margin-bottom: 0px;
}

.sub-label{
  padding-left: 2px;
    font-size: .775rem;
    color: #fd6825;
}

.country-profile-text p{
  font-family: 'Source Sans Pro', sans-serif;
  font-size: 1.5rem;
  font-weight: 500;
  color: #32325d;
}

.country-profile-title h5{
  text-transform: uppercase;
  color: black;
}

.sdg-goal-card{
  height: 100%;
}

.sdg-index-title{
  font-family: 'Source Sans Pro', sans-serif;
  font-size: 1.6rem;
  color: #02314e;
}

.about-page p{
  font-family: 'Source Sans Pro', sans-serif;
  font-size: 1.2rem;
  font-weight: 300;
  line-height: 1.7;
  color: #132654;
}

.aboutCards h5{
  font-family: 'Source Sans Pro', sans-serif;
  font-size: 1.5rem;
  font-weight: 600;
  letter-spacing: 1.05px;
}
.aboutCards{
  margin-top: 25px;
  margin-bottom: 60px;
}
